import React, { PureComponent } from 'react'
import "./MatchCard.scss"

class MatchCard extends PureComponent {
  render() {
    let {name,
        semelhanca,} = this.props
    return (
      <div className="mcd_1">
        <div className="mcd_2">
        <img className="mcd_4" src={require("../img/pexels-photo-555790.png")} alt=""/>
        </div>
        <div className="mcd_3">
        <div className="row">
        <div className="col-10">
        <div className="mcd_5">{name}</div>
        <div className="mcd_6">Semelhança: {semelhanca}</div>
        </div>
        <div className="col-2 mcd_7">
        <i className="material-icons">chat</i>
        </div>
        </div>
        </div>
      </div>
    )
  }
}
export default MatchCard