import React, { PureComponent } from 'react'
import './Chatters.scss';

const Circle = props => {
  return(
    <div className="cs_2">
    <img className="cs_1" src={require("../img/pexels-photo-555790.png")} alt=""/>
    </div>
  )
}

const AddOne = props => {
  return(
    <div className="cs_4">
    +1
    </div>
  )
}

export default class Chatters extends PureComponent {
  render() {
    return (
      <div className="col-1 cs_3">
        <Circle/>
        <Circle/>
        <Circle/>
        <Circle/>
        <Circle/>
        <AddOne/>
      </div>
    )
  }
}
