import React, { Component } from 'react';
import { Switch, Route } from 'react-router'
import './App.css';
import ChatPage from './components/ChatPage';
import MatchsPage from './components/MatchsPage';
import ProfileConfigs from './components/ProfileConfigs';

class App extends Component {
  render() {
    return (
      <div className="App">
      <Switch>
      <Route path="/" exact component={ChatPage}/>
      <Route path="/matchs" component={MatchsPage}/>
      <Route path="/configs" component={ProfileConfigs}/>
      </Switch>
      </div>
    );
  }
}

export default App;
