import React, { PureComponent } from 'react'
import "./MatchsPage.scss"
import MatchCard from './MatchCard';
import {connect} from "react-redux"
import GooeyNav from './GooeyNav';

class MatchsPage extends PureComponent {
  constructor(props){
    super(props)
    this.state={
      data: [],
      per: 3,
      page: 0,
      total_pages: null,
    }
  }

  componentDidMount(){
    this.loadUser()
    this.scrollListener = window.addEventListener("scroll", e => {
      this.handleScroll();
    });
  }

  handleScroll = () => { 
      var lastItem = document.querySelector(".mpe_2 > div:last-child");
      var container = document.querySelector(".mpe_2");
      var lastItenOffset = lastItem.offsetTop + lastItem.clientHeight;
      var pageOffset = container.pageYOffset + container.innerHeight;
      console.log(container.clientHeight, "lastItenOffset: ", lastItenOffset,"pageOffset: ",pageOffset) ;
      
      if (pageOffset > lastItenOffset) {
        this.loadMore();
      }
  };

  loadMore = () => {
    this.setState(
      prevState => ({
        page: prevState.page + 1,
        scrolling: true
      }),
      this.loadUser
    );
  };

  loadUser = () => {
    let offset = 18
    this.setState((state,props)=>{
      return{
        ...state,
        data: [...state.data,...props.matchsInfo.slice(state.page*18,state.page*18+offset)],
        scrolling: false,
      }  
  })
};

  render() {
    
    let {data} = this.state
    console.log(data, this.props.matchsInfo);
    
    return (
      <div>
        <GooeyNav/>
          <div className="mpe_2"
          onScroll={this.handleScroll}>
          <div className="row">
          <div className="mpe_1">Matchs</div>
          </div>
          <div className="row">
          {data.map((item, idx) => <MatchCard key={idx} {...item}/>)}
          </div>
          <button className="mpe_3"
          onClick={e => {
            this.loadMore();
          }}>
          Carregar mais...
        </button>
        <div></div>
          </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
    matchsInfo: state.matchs.peopleMatchs,
  }
}


export default connect(mapStateToProps,null)(MatchsPage)