import React, { PureComponent } from 'react'
import ReactDOM from "react-dom"
import "./ProfileConfigs.scss"
import "../utils/resize"

import Cropper from 'react-easy-crop'
import GooeyNav from './GooeyNav';
const idModal = document.getElementById("modal")

class PhotoChange extends PureComponent {
    constructor(props){
        super(props)
        this.state={
            image: 'https://img.huffingtonpost.com/asset/5ab4d4ac2000007d06eb2c56.jpeg?cache=sih0jwle4e&ops=1910_1000',
            crop: { x: 0, y:0 },
            zoom: 1,
            aspect: 1,
        }
        this.el = document.createElement("div")
    }
    onCropChange = crop => {
        this.setState({ crop })
      }
    componentDidMount(){
        idModal.appendChild(this.el)
    }
    componentWillUnmount(){
        idModal.removeChild(this.el)
    }
    
  render() {
    return ReactDOM.createPortal (
        <div className="pcs_12">
        <div className="pcs_14">
        <label htmlFor="file-upload">Selecionar foto</label> 
        <input 
        onChange={(e)=>this.props._handleImageChange(e)} 
        type="file" name="foto" id="file-upload"/>
        </div>
        <div className="pcs_15">
        <Cropper
            image={this.props.file}
            crop={this.state.crop}
            zoom={this.state.zoom}
            aspect={this.state.aspect}
            onCropChange={this.onCropChange}
            onCropComplete={this.props.onCropComplete}
            />
        </div>
        </div>,this.el
    )
  }
}

class ProfileConfigs extends PureComponent {

    constructor(props){
        super(props)
        this.state={
            file: '',
            imagePreviewUrl: '',
            modal:false,
        }
        this.profilePic=React.createRef()
    }

    componentDidMount(){

    }

    createImage = url =>
        new Promise((resolve, reject) => {
        const image = new Image()
        image.addEventListener('load', () => resolve(image))
        image.addEventListener('error', error => reject(error))
        image.setAttribute('crossOrigin', 'anonymous') // needed to avoid cross-origin issues on CodeSandbox
        image.src = url
    })

    getCroppedImg=async (imageSrc, pixelCrop)=>{
        const image = await this.createImage(imageSrc)
        const canvas = document.createElement('canvas')
        canvas.width = pixelCrop.width
        canvas.height = pixelCrop.height
        const ctx = canvas.getContext('2d')
      
        ctx.drawImage(
          image,
          pixelCrop.x,
          pixelCrop.y,
          pixelCrop.width,
          pixelCrop.height,
          0,
          0,
          pixelCrop.width,
          pixelCrop.height
        )
      
        // As Base64 string 
        return canvas.toDataURL('image/jpeg');
            
        // As a blob
        // return new Promise((resolve, reject) => {
        //   canvas.toBlob(file => {
        //     resolve(URL.createObjectURL(file))
        //   }, 'image/jpeg')
        // })
      }

    onCropComplete = async(croppedArea, croppedAreaPixels) =>{
        console.log(croppedArea, croppedAreaPixels)
        const croppedImage = await this.getCroppedImg(this.state.imagePreviewUrl, croppedAreaPixels)
        this.profilePic.current.src=croppedImage
        this.setState({croppedAreaPixels})
        }

    toggleModal = () =>{
        this.setState({
            ...this.state,
            modal: !this.state.modal
        })
    }

    _handleImageChange=(e) => {
        e.preventDefault();
    
        let reader = new FileReader();
        let file = e.target.files[0];
        console.log(reader.result);
        
        reader.onloadend = () => {
            this.setState({
            file: file,
            imagePreviewUrl: reader.result
          });
        }
    
        reader.readAsDataURL(file)
      }


  render() {

    let {imagePreviewUrl} = this.state;
    
    return (
      <div className="pcs_13">
      <GooeyNav/>
        {this.state.modal && <PhotoChange 
        file={imagePreviewUrl}
        _handleImageChange={this._handleImageChange}
        onCropComplete={this.onCropComplete}/>}
        <div className="mpe_1">Configurações</div>
        <div className="row pcs_16">
        <div className="col-3">
        <div className="pcs_2">
        <div className="pcs_9"
        onClick={this.toggleModal}>
        <div className="pcs_11">
        Alterar Foto
        </div>
        </div>
        <img ref={this.profilePic} className="pcs_1" src={require("../img/pexels-photo-555790.png")} alt=""/>
        </div>
        <div className="pcs_5">
        <div className="pcs_6">
        Renato Nankran Magalhães
        </div>
        <div className="pcs_7">
        Preferência de local: Caiçara, Belo Horizonte
        </div>
        <div className="pcs_7">
        Valor: $800,00
        </div>
        </div>
        </div>
        <div className="col-7 col-offset-1 pcs_8">
        <div className="pcs_10">
        <label htmlFor="name">Nome*</label>
        <input type="text" name="name" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="surname">Sobrenome*</label>
        <input type="text" name="surname" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="email">E-mail*</label>
        <input type="text" name="email" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="email">Valor maximo</label>
        <input type="text" name="maxval" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="email">Preferência de local</label>
        <input type="text" name="place" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="phone">Telefone</label>
        <input type="text" name="phone" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="facebook">Facebook</label>
        <input type="text" name="facebook" id=""/>
        </div>
        <div className="pcs_10">
        <label htmlFor="instagram">Instagram</label>
        <input type="text" name="instagram" id=""/>
        </div>
        <button>Salvar</button>
        </div>
        </div>
      </div>
    )
  }
}
export default ProfileConfigs